<template>
  <div class="edit-box">
    <div class="header">
      <span></span>
      <el-switch v-model="config.visible" @change="onChangeAnimationSwitch" />
    </div>
    <!--  -->
    <div class="options" :class="optionDisabled">
      <el-scrollbar max-height="250px" v-if="state.modelAnimation.length">
        <div class="option" :class="config.animationName == animation.name ? 'option-active' : ''"
          @click="onChangeAnimationType(animation)" v-for="animation in state.modelAnimation" :key="animation.name">
          <el-space>
            <div class="icon-name">
              {{ animation.name }}
            </div>
            <div class="check" v-show="config.animationName == animation.name">
              <el-icon size="20px" color="#2a3ff6">
                <Check />
              </el-icon>
            </div>
          </el-space>
        </div>
      </el-scrollbar>
      <el-empty v-else description="" :image-size="120" />
    </div>
    <!--  -->
    <div class="header">
      <span></span>
    </div>
    <div class="options" :class="optionDisabled">
      <div class="option">
        <el-space>
          <el-icon>
            <Switch />
          </el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option">
        <el-radio-group v-model="config.loop" @change="onUplateAnimation">
          <el-radio-button label="LoopOnce"></el-radio-button>
          <el-radio-button label="LoopRepeat"></el-radio-button>
          <el-radio-button label="LoopPingPong"></el-radio-button>
        </el-radio-group>
      </div>
      <div class="option">
        <el-space>
          <el-icon>
            <VideoPlay />
          </el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option">
        <el-slider show-input v-model="config.timeScale" @change="onUplateAnimation" :step="0.01" :min="0" :max="1" />
      </div>
      <div class="option">
        <el-space>
          <el-icon>
            <Sort />
          </el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option">
        <el-slider show-input v-model="config.weight" @change="onUplateAnimation" :step="0.01" :min="0" :max="1" />
      </div>
    </div>
    <!--  -->
    <div class="header">
      <span></span>
      <el-switch v-model="config.rotationVisible" @change="onRotationAnimation" />
    </div>
    <div class="options">
      <div class="option" :class="optionRotation">
        <el-space>
          <span>  </span>
          <el-radio-group v-model="config.rotationType" @change="onRotationType">
            <el-radio-button label="x">X</el-radio-button>
            <el-radio-button label="y">Y</el-radio-button>
            <el-radio-button label="z">Z</el-radio-button>
          </el-radio-group>
        </el-space>
      </div>
      <div class="option" :class="optionRotation">
        <el-space>
          <el-icon>
            <VideoPlay />
          </el-icon>
          <span>  </span>
        </el-space>
      </div>
      <div class="option" :class="optionRotation">
        <el-slider @input="onRotationAnimation" show-input v-model="config.rotationSpeed" :step="0.01" :min="1"
          :max="10" />
      </div>
    </div>
  </div>
</template>
<script setup lang="ts">
import { reactive, computed, onMounted, getCurrentInstance } from "vue";
import { useStore } from "vuex";
const store = useStore();
const { $bus } = (getCurrentInstance() as any).proxy;
const config = reactive({
  visible: false,
  animationName: null, //
  loop: "LoopRepeat", //  TODO:LoopOnce  LoopRepeat   LoopPingPong 
  timeScale: 1, // 
  weight: 1, // 
  rotationVisible: false, //
  rotationType: "y", // 
  rotationSpeed: 1, // 
});
const optionDisabled = computed(() => {
  return config.visible ? "" : "disabled";
});
const optionRotation = computed(() => {
  return config.rotationVisible ? "" : "disabled";
});

const state = reactive({
  modelApi: computed(() => {
    return store.state.modelApi;
  }),
  // 
  modelAnimation: computed(() => {
    if (store.state.modelApi) {
      return store.state.modelApi.modelAnimation;
    }
    return [];
  }),
});

onMounted(() => {
  if (state.modelAnimation.length) {
    config.animationName = state.modelAnimation[0].name;
  }
  // 
  $bus.on("model-update", () => {
    if (state.modelAnimation.length) {
      const animationName = state.modelAnimation[0].name;
      config.animationName = animationName;
    }
    // 
    Object.assign(config, {
      visible: false,
      loop: "LoopRepeat", //  TODO:LoopOnce  LoopRepeat   LoopPingPong 
      timeScale: 1, // 
      weight: 1, // 
      rotationVisible: false, //
      rotationType: "y", // 
      rotationSpeed: 1, // 
    });
  });
});

// /
const onChangeAnimationSwitch = () => {
  const { visible } = config;
  if (visible) {
    onUplateAnimation();
  } else {
    state.modelApi.onClearAnimation();
  }
};
// 
const onChangeAnimationType = (animation: { name: any; }) => {
  const { name } = animation
  config.animationName = name;
  state.modelApi.onStartModelAnimaion(config);
};
// 
const onUplateAnimation = () => {
  state.modelApi.onStartModelAnimaion(config);
};

// 
const onRotationAnimation = () => {
  state.modelApi.onSetRotation(config);
};
// 
const onRotationType = () => {
  state.modelApi.onSetRotationType(config);
};

defineExpose({
  config,
});
</script>
<style scoped lang="scss"></style>
